"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";

const StaffPage = () => {
  const params = useParams();
  const router = useRouter();
  const { id } = params;

  const [staff, setStaff] = useState<any[]>([]);
  const [filteredStaff, setFilteredStaff] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [sortBy, setSortBy] = useState("name");
  const [positionFilter, setPositionFilter] = useState("all");
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 15;

  const pageBackgroundStyle = {
    position: "fixed" as const,
    top: 0,
    left: 0,
    width: "100vw",
    height: "100vh",
    background: "linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%)",
    backgroundAttachment: "fixed",
    zIndex: -1
  };

  const contentStyle = {
    position: "relative" as const,
    zIndex: 1,
    padding: "20px",
    paddingTop: "80px",
    minHeight: "100vh"
  };

  useEffect(() => {
    async function loadStaff() {
      if (!id) return;
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}/staff`);
        const json = await res.json();
        const data = json.data || [];
        setStaff(data);
        setFilteredStaff(data);
      } catch (error) {
        console.error("Erro ao carregar staff:", error);
      } finally {
        setLoading(false);
      }
    }
    loadStaff();
  }, [id]);

  useEffect(() => {
    let results = staff;
    
    if (searchTerm.trim() !== "") {
      const term = searchTerm.toLowerCase();
      results = results.filter(person => 
        person.person.name.toLowerCase().includes(term) ||
        person.positions?.some((pos: string) => 
          pos.toLowerCase().includes(term)
        )
      );
    }
    
    if (positionFilter !== "all") {
      results = results.filter(person => 
        person.positions?.some((pos: string) => 
          pos.toLowerCase() === positionFilter.toLowerCase()
        )
      );
    }
    
    results.sort((a, b) => {
      if (sortBy === "name") {
        return a.person.name.localeCompare(b.person.name);
      } else if (sortBy === "favorites") {
        return (b.person.favorites || 0) - (a.person.favorites || 0);
      }
      return 0;
    });
    
    setFilteredStaff(results);
    setCurrentPage(1);
  }, [searchTerm, positionFilter, sortBy, staff]);

  const totalPages = Math.ceil(filteredStaff.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const paginatedStaff = filteredStaff.slice(startIndex, endIndex);

  const uniquePositions = Array.from(
    new Set(staff.flatMap(person => person.positions || []))
  ).sort();

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <>
      <div style={pageBackgroundStyle} />
      
      <div style={contentStyle}>
        <div style={{ maxWidth: "1200px", margin: "0 auto" }}>
          {/* Cabe√ßalho */}
          <div style={{ 
            display: "flex", 
            justifyContent: "space-between", 
            alignItems: "center", 
            marginBottom: 30,
            flexWrap: "wrap",
            gap: 15
          }}>
            <h1 style={{ 
              color: "white", 
              margin: 0, 
              fontSize: "2.2rem",
              textShadow: "0 2px 4px rgba(0,0,0,0.5)",
              background: "rgba(0, 0, 0, 0.6)",
              padding: "15px 25px",
              borderRadius: "12px",
              border: "2px solid rgba(255, 255, 255, 0.1)"
            }}>
               Staff do Anime
            </h1>
            
            <button 
              onClick={() => router.push(`/${id}`)}
              style={{ 
                padding: "12px 25px", 
                background: "linear-gradient(135deg, #1e88e5, #0d47a1)", 
                color: "white", 
                border: "none", 
                borderRadius: "10px", 
                cursor: "pointer",
                fontWeight: "bold",
                fontSize: "16px",
                transition: "all 0.3s",
                display: "flex",
                alignItems: "center",
                gap: "10px",
                boxShadow: "0 4px 15px rgba(30, 136, 229, 0.4)"
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.transform = "translateY(-3px)";
                e.currentTarget.style.boxShadow = "0 8px 20px rgba(30, 136, 229, 0.6)";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.transform = "translateY(0)";
                e.currentTarget.style.boxShadow = "0 4px 15px rgba(30, 136, 229, 0.4)";
              }}
            >
              <span>‚Üê</span>
              <span>Voltar ao Anime</span>
            </button>
          </div>

          {/* Filtros */}
          <div style={{ 
            background: "rgba(0, 0, 0, 0.8)", 
            padding: "25px", 
            borderRadius: "15px",
            marginBottom: "30px",
            backdropFilter: "blur(10px)",
            border: "2px solid rgba(255, 255, 255, 0.15)",
            boxShadow: "0 10px 30px rgba(0, 0, 0, 0.4)"
          }}>
            <div style={{ 
              display: "flex", 
              flexDirection: "column",
              gap: "20px"
            }}>
              {/* Barra de busca e filtros */}
              <div style={{ display: "flex", gap: "15px", flexWrap: "wrap" }}>
                <input
                  type="text"
                  placeholder="üîç Buscar por nome ou posi√ß√£o..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  style={{
                    padding: "14px 20px",
                    borderRadius: "10px",
                    border: "2px solid rgba(255, 255, 255, 0.2)",
                    background: "rgba(0, 0, 0, 0.9)",
                    color: "white",
                    flex: "1",
                    minWidth: "250px",
                    outline: "none",
                    fontSize: "16px",
                    transition: "all 0.3s"
                  }}
                  onFocus={(e) => e.target.style.borderColor = "#1e88e5"}
                  onBlur={(e) => e.target.style.borderColor = "rgba(255, 255, 255, 0.2)"}
                />
                
                {/* Select de Posi√ß√£o */}
                <select
                  value={positionFilter}
                  onChange={(e) => setPositionFilter(e.target.value)}
                  style={{
                    padding: "14px 45px 14px 20px",
                    borderRadius: "10px",
                    border: "2px solid rgba(255, 255, 255, 0.3)",
                    background: "rgba(0, 0, 0, 0.9)",
                    color: "white",
                    minWidth: "180px",
                    outline: "none",
                    cursor: "pointer",
                    fontSize: "16px",
                    fontWeight: "500",
                    transition: "all 0.3s",
                    appearance: "none",
                    backgroundImage: `url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='white'%3e%3cpath d='M7 10l5 5 5-5z'/%3e%3c/svg%3e")`,
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "right 15px center",
                    backgroundSize: "20px"
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.borderColor = "#1e88e5"}
                  onMouseLeave={(e) => e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.3)"}
                >
                  <option value="all"> Todas as posi√ß√µes</option>
                  {uniquePositions.map((position, index) => (
                    <option key={index} value={position}>
                      {position}
                    </option>
                  ))}
                </select>
                
                {/* Select de Ordena√ß√£o */}
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value)}
                  style={{
                    padding: "14px 45px 14px 20px",
                    borderRadius: "10px",
                    border: "2px solid rgba(255, 255, 255, 0.3)",
                    background: "rgba(0, 0, 0, 0.9)",
                    color: "white",
                    minWidth: "180px",
                    outline: "none",
                    cursor: "pointer",
                    fontSize: "16px",
                    fontWeight: "500",
                    transition: "all 0.3s",
                    appearance: "none",
                    backgroundImage: `url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='white'%3e%3cpath d='M7 10l5 5 5-5z'/%3e%3c/svg%3e")`,
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "right 15px center",
                    backgroundSize: "20px"
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.borderColor = "#4ECDC4"}
                  onMouseLeave={(e) => e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.3)"}
                >
                  <option value="name"> Ordenar por nome</option>
                  <option value="favorites">Ordenar por favoritos</option>
                </select>
              </div>
              
              {/* Info e a√ß√µes */}
              <div style={{ 
                display: "flex", 
                justifyContent: "space-between",
                alignItems: "center",
                color: "white",
                fontSize: "16px",
                flexWrap: "wrap",
                gap: "15px"
              }}>
                <div style={{ display: "flex", alignItems: "center", gap: "15px", flexWrap: "wrap" }}>
                  <span style={{ 
                    color: "#ffffff",
                    background: "rgba(30, 136, 229, 0.2)",
                    padding: "8px 16px",
                    borderRadius: "8px",
                    fontWeight: "bold"
                  }}>
                     {filteredStaff.length} membro{filteredStaff.length !== 1 ? 's' : ''} do staff
                  </span>
                  
                  {searchTerm && (
                    <span style={{ 
                      color: "#90caf9",
                      background: "rgba(144, 202, 249, 0.1)",
                      padding: "8px 16px",
                      borderRadius: "8px"
                    }}>
                       "{searchTerm}"
                    </span>
                  )}
                  
                  {positionFilter !== "all" && (
                    <span style={{ 
                      color: "#81c784",
                      background: "rgba(129, 199, 132, 0.1)",
                      padding: "8px 16px",
                      borderRadius: "8px"
                    }}>
                       {positionFilter}
                    </span>
                  )}
                  
                  <span style={{ 
                    color: "#ffb74d",
                    background: "rgba(255, 183, 77, 0.1)",
                    padding: "8px 16px",
                    borderRadius: "8px"
                  }}>
                     P√°gina {currentPage} de {totalPages}
                  </span>
                </div>
                
                {(searchTerm || positionFilter !== "all") && (
                  <button
                    onClick={() => {
                      setSearchTerm("");
                      setPositionFilter("all");
                      setSortBy("name");
                    }}
                    style={{
                      padding: "10px 20px",
                      background: "linear-gradient(135deg, #ff4444, #cc0000)",
                      color: "white",
                      border: "none",
                      borderRadius: "8px",
                      cursor: "pointer",
                      fontSize: "15px",
                      fontWeight: "bold",
                      transition: "all 0.3s",
                      display: "flex",
                      alignItems: "center",
                      gap: "8px"
                    }}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.transform = "translateY(-2px)";
                      e.currentTarget.style.boxShadow = "0 6px 20px rgba(255, 68, 68, 0.4)";
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.transform = "translateY(0)";
                      e.currentTarget.style.boxShadow = "none";
                    }}
                  >
                     Limpar filtros
                  </button>
                )}
              </div>
            </div>
          </div>

          {/* Loading */}
          {loading ? (
            <div style={{ 
              textAlign: "center", 
              padding: "80px 20px",
              background: "rgba(0, 0, 0, 0.7)",
              borderRadius: "15px",
              backdropFilter: "blur(10px)",
              border: "2px solid rgba(255, 255, 255, 0.1)"
            }}>
              <div style={{ 
                width: "70px", 
                height: "70px", 
                border: "6px solid rgba(255, 255, 255, 0.1)",
                borderTop: "6px solid #1e88e5",
                borderRadius: "50%",
                margin: "0 auto 25px",
                animation: "spin 1s linear infinite"
              }} />
              <p style={{ 
                color: "white", 
                fontSize: "20px",
                textShadow: "0 2px 4px rgba(0,0,0,0.5)"
              }}>
                Carregando staff...
              </p>
            </div>
          ) : paginatedStaff.length === 0 ? (
            <div style={{ 
              textAlign: "center", 
              padding: "80px 20px",
              background: "rgba(0, 0, 0, 0.7)",
              borderRadius: "15px",
              backdropFilter: "blur(10px)",
              border: "2px solid rgba(255, 255, 255, 0.1)"
            }}>
              <div style={{ fontSize: "60px", marginBottom: "20px" }}>üòï</div>
              <p style={{ 
                color: "white", 
                fontSize: "22px", 
                marginBottom: "15px",
                fontWeight: "bold"
              }}>
                Nenhum membro do staff encontrado
              </p>
              <p style={{ 
                color: "#aaa", 
                fontSize: "16px", 
                marginBottom: "30px",
                maxWidth: "600px",
                margin: "0 auto"
              }}>
                {searchTerm || positionFilter !== "all" 
                  ? `Tente ajustar os filtros de busca.`
                  : "Este anime n√£o possui informa√ß√µes de staff."}
              </p>
              {(searchTerm || positionFilter !== "all") && (
                <button
                  onClick={() => {
                    setSearchTerm("");
                    setPositionFilter("all");
                    setSortBy("name");
                  }}
                  style={{
                    padding: "14px 28px",
                    background: "linear-gradient(135deg, #1e88e5, #0d47a1)",
                    color: "white",
                    border: "none",
                    borderRadius: "10px",
                    cursor: "pointer",
                    fontSize: "16px",
                    fontWeight: "bold",
                    transition: "all 0.3s",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                    margin: "0 auto"
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.05)"}
                  onMouseLeave={(e) => e.currentTarget.style.transform = "scale(1)"}
                >
                   Limpar filtros
                </button>
              )}
            </div>
          ) : (
            <>
              {/* Grid de Staff */}
              <div style={{ 
                display: "grid", 
                gridTemplateColumns: "repeat(auto-fill, minmax(350px, 1fr))", 
                gap: 25,
                marginBottom: "40px"
              }}>
                {paginatedStaff.map((person: any) => (
                  <div key={person.person.mal_id} style={{ 
                    background: "rgba(0, 0, 0, 0.7)", 
                    borderRadius: "15px", 
                    padding: "25px",
                    backdropFilter: "blur(10px)",
                    border: "1px solid rgba(255, 255, 255, 0.1)",
                    transition: "all 0.3s ease",
                    cursor: "pointer"
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.transform = "translateY(-5px)";
                    e.currentTarget.style.boxShadow = "0 10px 30px rgba(0, 0, 0, 0.3)";
                    e.currentTarget.style.borderColor = "#1e88e5";
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.transform = "translateY(0)";
                    e.currentTarget.style.boxShadow = "none";
                    e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.1)";
                  }}
                  >
                    <div style={{ display: "flex", gap: 20, alignItems: "flex-start" }}>
                      <img 
                        src={person.person.images.jpg.image_url} 
                        alt={person.person.name}
                        style={{ 
                          width: "100px", 
                          height: "120px", 
                          objectFit: "cover", 
                          borderRadius: "10px",
                          border: "2px solid #1e88e5"
                        }}
                      />
                      <div style={{ flex: 1 }}>
                        <h3 style={{ 
                          color: "white", 
                          margin: "0 0 15px 0",
                          fontSize: "20px",
                          fontWeight: "bold"
                        }}>
                          {person.person.name}
                        </h3>
                        
                        <div style={{ 
                          display: "flex", 
                          flexWrap: "wrap", 
                          gap: 8,
                          marginBottom: 15
                        }}>
                          {person.positions?.map((position: string, idx: number) => (
                            <span
                              key={idx}
                              style={{
                                background: position.toLowerCase().includes("director") ? "rgba(30, 136, 229, 0.9)" :
                                          position.toLowerCase().includes("writer") ? "rgba(67, 160, 71, 0.9)" :
                                          position.toLowerCase().includes("producer") ? "rgba(244, 81, 30, 0.9)" :
                                          position.toLowerCase().includes("design") ? "rgba(142, 36, 170, 0.9)" :
                                          "rgba(85, 85, 85, 0.9)",
                                color: "white",
                                padding: "6px 12px",
                                borderRadius: "20px",
                                fontSize: "13px",
                                fontWeight: "bold"
                              }}
                            >
                              {position}
                            </span>
                          ))}
                        </div>
                        
                        <div style={{ color: "#aaa", fontSize: "15px" }}>
                          <p style={{ margin: "8px 0" }}>
                            <strong style={{ color: "#90caf9" }}> Favoritos:</strong> {person.person.favorites?.toLocaleString() || "N/A"}
                          </p>
                          {person.person.website_url && (
                            <a 
                              href={person.person.website_url} 
                              target="_blank" 
                              rel="noopener noreferrer"
                              style={{ 
                                color: "#1e88e5", 
                                textDecoration: "none",
                                display: "inline-flex",
                                alignItems: "center",
                                gap: "5px",
                                fontWeight: "bold"
                              }}
                            >
                               Site Oficial
                            </a>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              {/* Pagina√ß√£o */}
              {totalPages > 1 && (
                <div style={{ 
                  display: "flex", 
                  justifyContent: "center", 
                  alignItems: "center",
                  gap: "10px",
                  flexWrap: "wrap",
                  marginBottom: "40px",
                  padding: "25px",
                  background: "rgba(0, 0, 0, 0.7)",
                  borderRadius: "15px",
                  border: "2px solid rgba(255, 255, 255, 0.1)"
                }}>
                  <button
                    onClick={() => handlePageChange(currentPage - 1)}
                    disabled={currentPage === 1}
                    style={{
                      padding: "12px 25px",
                      background: currentPage === 1 
                        ? "rgba(255, 255, 255, 0.1)" 
                        : "linear-gradient(135deg, #1e88e5, #0d47a1)",
                      color: "white",
                      border: "none",
                      borderRadius: "8px",
                      cursor: currentPage === 1 ? "not-allowed" : "pointer",
                      fontWeight: "bold",
                      fontSize: "16px",
                      opacity: currentPage === 1 ? 0.5 : 1,
                      transition: "all 0.3s"
                    }}
                  >
                    ‚Üê Anterior
                  </button>

                  {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                    let pageNum;
                    if (totalPages <= 5) {
                      pageNum = i + 1;
                    } else if (currentPage <= 3) {
                      pageNum = i + 1;
                    } else if (currentPage >= totalPages - 2) {
                      pageNum = totalPages - 4 + i;
                    } else {
                      pageNum = currentPage - 2 + i;
                    }

                    return (
                      <button
                        key={pageNum}
                        onClick={() => handlePageChange(pageNum)}
                        style={{
                          padding: "12px 18px",
                          minWidth: "45px",
                          background: currentPage === pageNum 
                            ? "linear-gradient(135deg, #4ECDC4, #1CE669)" 
                            : "rgba(255, 255, 255, 0.1)",
                          color: "white",
                          border: currentPage === pageNum 
                            ? "2px solid #ffffff" 
                            : "1px solid rgba(255, 255, 255, 0.3)",
                          borderRadius: "8px",
                          cursor: "pointer",
                          fontWeight: "bold",
                          fontSize: "16px",
                          transition: "all 0.3s"
                        }}
                        onMouseEnter={(e) => {
                          if (currentPage !== pageNum) {
                            e.currentTarget.style.background = "rgba(255, 255, 255, 0.2)";
                          }
                        }}
                      >
                        {pageNum}
                      </button>
                    );
                  })}

                  <button
                    onClick={() => handlePageChange(currentPage + 1)}
                    disabled={currentPage === totalPages}
                    style={{
                      padding: "12px 25px",
                      background: currentPage === totalPages 
                        ? "rgba(255, 255, 255, 0.1)" 
                        : "linear-gradient(135deg, #FF6B6B, #FF8E53)",
                      color: "white",
                      border: "none",
                      borderRadius: "8px",
                      cursor: currentPage === totalPages ? "not-allowed" : "pointer",
                      fontWeight: "bold",
                      fontSize: "16px",
                      opacity: currentPage === totalPages ? 0.5 : 1,
                      transition: "all 0.3s"
                    }}
                  >
                    Pr√≥xima ‚Üí
                  </button>

                  <div style={{ 
                    color: "#ffffff", 
                    fontSize: "16px", 
                    marginLeft: "20px",
                    background: "rgba(0, 0, 0, 0.5)",
                    padding: "10px 20px",
                    borderRadius: "8px",
                    border: "1px solid rgba(255, 255, 255, 0.2)"
                  }}>
                    P√°gina <span style={{ color: "#4ECDC4", fontWeight: "bold" }}>{currentPage}</span> de {totalPages}
                  </div>
                </div>
              )}
            </>
          )}
        </div>
      </div>
      <style jsx>{`
        @keyframes spin {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }
      `}</style>
    </>
  );
};

export default StaffPage;